.header {
  position: absolute;
  top: 0;
  left: 0;
  width: 100vw;
  padding: 20px 0;
  z-index: 999;
  transition: all .5s linear 0s;
  &.active {
    background: $white;
    box-shadow: 5px 5px 15px 0 rgba(31, 31, 31, 0.07);
    top: -100px;
    .header__nav ul li, .header__nav ul a {
      color: $dark; }
    .header__btn .btn-secondary {
      color: $dark; } }
  &.scroll {
    top: 0; }
  &.app-function {
    .header__nav ul li, .header__nav ul a {
      color: $dark; }
    .header__btn .btn-secondary {
      color: $dark; } }
  .menu-open {
    position: absolute;
    right: 20px;
    top: 49%;
    width: 30px;
    height: 30px;
    display: none;
    z-index: 99;
    transform: translate(0, -50%) scale(0.8);
    @include r(1040) {
      display: block; }
    span {
      width: 30px;
      height: 2px;
      margin-top: 10px;
      background: $cerise;
      display: block;
      &:before, &:after {
        content: '';
        width: 30px;
        height: 2px;
        background: $cerise;
        position: absolute;
        left: 0; }
      &:before {
        top: 0; }
      &:after {
        bottom: 8px; } } }
  &__content {
    display: flex;
    align-items: center; }
  &__logo {
    margin-right: 60px;
    margin-top: 10px;
    img {
      height: 40px; } }
  &__nav {
    display: flex;
    width: 100%;
    align-items: center;
    nav {
      flex: 1; }
    ul {
      display: flex;
      justify-content: space-between;
      a, li {
        font-size: 14px;
        font-weight: bold;
        color: $white;
        position: relative;
        display: block;
        i {
          font-size: 10px;
          position: relative;
          right: -1.4rem;
          @include tr;
          color: #9e9e9e; } }
      li {
        &.active {
          i {
            transform: rotate(180deg); } }
        &:hover {
          .header__nav__dropdown {
            opacity: 1;
            visibility: visible; } } } }
    &__dropdown {
      width: 365px;
      position: absolute;
      box-shadow: 0 0 10px 0 rgba(0,0,0,.2);
      background: $white;
      right: -265px;
      top: 25px;
      border-radius: 10px;
      flex-direction: column;
      @include tr;
      opacity: 0;
      visibility: hidden;
      &--icon {
        width: 445px;
        right: -308px; }
      &:after {
        content: '';
        width: 100%;
        height: 10px;
        position: absolute;
        top: -10px; }
      &:before {
        content: '';
        border: 8px solid transparent;
        border-bottom: 8px solid $white;
        position: absolute;
        top: -16px;
        left: 30%; }
      &__img {
        width: 75px;
        img {
          height: 50px;
          margin-right: 30px; } }
      &__list {
        flex: 1 0; }
      li {
        cursor: pointer;
        &:first-child {
          a {
            border-top-left-radius: 10px;
            border-top-right-radius: 10px; } }
        &:last-child {
          a {
            border-bottom-left-radius: 10px;
            border-bottom-right-radius: 10px;
            border-bottom: none; } } }
      a {
        padding: 23px 33px;
        display: flex;
        border-bottom: 1px solid rgba($pinkish-grey, 0.5);
        &:hover {
          background-color: $white-three; }
        b {
          font-size: 14px;
          font-weight: 600;
          color: $dark;
          margin-bottom: 10px;
          display: block;
          span {
            font-size: 12px; } }
        p {
          font-size: 12px;
          line-height: 1.46;
          font-weight: normal;
          color: $brownish-grey-two;
          display: block;
          span {
            color: $dark-two;
            display: block;
            font-weight: normal; } } } } }
  &__btn {
    display: flex;
    justify-content: flex-end;
    align-items: center;
    @include r(1040) {
      padding-right: 60px; }
    a {
      font-size: 14px;
      font-weight: 600;
      line-height: 1.79;
      color: #ffffff;
      display: flex;
      align-items: center;
      &:hover {
        color: #ff0057; }
      &:first-child {
        margin-right: 30px;
        i {
          font-size: 11px;
          margin-right: 12px;
          color: #ff0057; } }
      &:nth-child(2) {
        i {
          width: 15px;
          height: 14px;
          background: #ff0057;
          color: #fff;
          @include center;
          border-radius: 3px;
          font-size: 6px;
          margin-right: 10px;
          margin-bottom: 2px; } }
      &.btn {
        border-radius: 23px;
        border: solid 1px #ff0057;
        background: none;
        font-size: 14px;
        font-weight: 600;
        color: #ff0057;
        margin-left: 15px;
        &:hover {
          background-color: #ff0057;
          color: $white; } } } } }
